@media screen and (min-width: 768px) {
  .menu {
    display: none;
  }
}

@media screen and (max-width: 767.98px) {
  body.noscroll {
    overflow: hidden;
  }

  .menu {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 50px;
    left: -110%;
    width: 100%;
    height: 100vh;
    padding: 20px;
    z-index: 90;
    background: linear-gradient(rgb(79, 46, 232) 0%, rgb(220, 220, 220) 100%);
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .menu.active {
    left: 0;
  }

  .menu__autorization {
    display: inline-flex;
    align-items: center;
    margin-bottom: 126px;
    z-index: 3;
  }
  .menu__autorization.js-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
  }
  .menu__autorization-photo {
    width: 37px;
    height: 37px;
    margin-right: 9px;
    border-radius: 50%;
  }

  .menu__autorization-name {
    font-weight: 700;
    font-size: 22px;
    line-height: 1.32;
    letter-spacing: -0.02em;
    color: #ffffff;
  }

  .menu__list {
    // margin-bottom: 427px;
    text-align: center;
    margin-bottom: 126px;
    align-self: center;
    z-index: 3;
  }

  .menu__item:not(:last-child) {
    margin-bottom: 16px;
    // padding-right: 75px;
  }

  .menu__link {
    // align-items: center;
    // justify-content: center;

    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: calc(18 / 14);
    letter-spacing: -0.04em;
    width: 156px;
    height: 34px;
    border-radius: 36px;
    padding: 8px 16px;

    text-transform: uppercase;

    &:hover {
      color: var(--hover);
    }
  }

  .menu__link-home {
    color: var(--white-color-not-chenge);
    font-weight: 700;
    // margin-left: -30px;
  }

  .index .menu__link-home {
    background: var(--background-header-btn);
    color: var(--black-color-not-chenge);

    &:hover {
      color: var(--link-focus);
    }
  }

  .menu__link-shopping-list {
    color: var(--black-color-not-chenge);
  }

  .index .menu__link-shopping-list {
    background-color: transparent;
    color: var(--white-color-not-chenge);

    &:hover {
      color: var(--hover);
    }
  }

  .menu__link-shopping-list {
    background: var(--background-header-btn);

    &:hover {
      color: var(--link-focus);
    }
  }

  .index .menu__link__icon {
    fill: currentColor;
  }

  .menu__link__icon {
    margin-left: 4px;
    fill: currentColor;
  }

  .menu__authenticator-button {
    position: absolute;
    bottom: 100px;
    align-items: center;
    min-width: 118px;
    min-height: 48px;
    background-color: #fff;
    border: 1.5px solid #111111;
    border-radius: 18px;
    margin-left: 20px;
    stroke: var(--background-header-btn);

    z-index: 80;
  }

  .menu__authenticator-button:focus,
  .menu__authenticator-button:hover {
    background-color: var(--background-header-btn);
    border-color: var(--hover);
    stroke: var(--link-focus);
  }

  .menu__authenticator-button.js-hidden {
    display: none;
    position: relative;
    left: -162px;
  }

  .menu__authenticator-button-text {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: calc(18 / 14);
    letter-spacing: -0.04em;
    color: #111111;
    margin-left: 14px;
    margin-right: auto;
  }

  .menu__authenticator-button-icon {
    margin-right: 34px;
  }

  .menu__img {
    position: absolute;
    // top: 400px;
    left: 0px;
    bottom: 0;
    width: 100%;

    z-index: 2;
  }

  .close {
    display: none;
    width: 28px;
    height: 28px;
    margin-left: 14px;
  }

  .hidden {
    display: block;
  }

  .visible {
    display: block;
  }

  .visible {
    display: block;
  }
}
