.backdrop {
  z-index: 80;
  position: fixed;

  display: flex;
  justify-content: center;
  align-items: center;

  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(17, 17, 17, 0.4);

  &.is-hidden .modal {
    display: none;
  }

  @media screen and (max-height: 800px) and (max-width: 768px) {
    align-items: flex-start;
    overflow-y: scroll;

    &::-webkit-scrollbar {
      width: 14px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: var(--scroll);
      border-radius: 12px;
      width: 8px;
      height: 114px;
    }
  }
  @media screen and (max-height: 500px) and (min-width: 769px) {
    align-items: flex-start;
    overflow-y: scroll;

    &::-webkit-scrollbar {
      width: 14px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: var(--scroll);
      border-radius: 12px;
      width: 8px;
      height: 114px;
    }
  }
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  position: absolute;
}

.modal {
  @media screen and (max-height: 800px) and (max-width: 768px) {
    margin: 20px auto;
  }

  @media screen and (max-height: 500px) and (min-width: 769px) {
    margin: 20px auto;
  }
  display: block;
  position: absolute;
  display: flex;
  flex-direction: column;

  width: 335px;
  border-radius: 18px;
  padding-left: 24px;
  padding-right: 24px;
  padding-top: 40px;
  padding-bottom: 40px;

  border: 2px solid var(--primary-text);
  background-color: var(--background-form);

  animation: flip-in-hor-bottom 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;

  @include media('phone', max) {
    width: 90%;
  }

  @media (min-width: 768px) {
    width: 579px;
    padding-left: 40px;
    padding-right: 40px;
  }
}
.modal_closes {
  display: block;
  fill: currentColor;
  stroke: var(--hover-modal-close);
  width: 28px;
  height: 28px;

  @include animated(stroke);

  &:hover {
    stroke: var(--hover);
  }
}

.modal_close {
  cursor: pointer;
  position: absolute;
  filter: var(--amazon-invert);
  top: 12px;
  right: 12px;

  @media (min-width: 768px) {
    right: 31px;
    top: 31px;
  }
}
.modal_top_section {
  @media (min-width: 768px) {
    display: flex;
    height: 281px;
    margin-bottom: 40px;
  }
}
.modal_info_section {
  @media (min-width: 768px) {
    margin-left: 24px;
    width: 297px;
  }
}
.modal_img {
  height: 100%;
  width: 100%;
}
.modal_img_placeholder {
  height: 70px;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.modal_shop_img_amazon {
  filter: var(--amazon-invert) grayscale(100%);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  @include animated(filter);

  &:hover,
  &:focus {
    filter: var(--amazon-invert) grayscale(0%)
      drop-shadow(5px 5px 5px var(--catigory-text));
  }
}
.modal_shop_img {
  filter: grayscale(100%);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  @include animated(filter);

  &:hover,
  &:focus {
    filter: grayscale(0%) drop-shadow(5px 5px 5px var(--catigory-text));
  }
}
.modal_cover {
  position: relative;
  width: 287px;
  height: 408px;
  border-radius: 8px;
  margin-bottom: 14px;

  @include media('phone', max) {
    width: 99%;
  }

  @media (min-width: 768px) {
    height: 281px;
    width: 192px;
  }
}
.modal_book_name {
  max-width: 275px;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.12;
  letter-spacing: -0.04em;
  margin-bottom: 4px;

  color: var(--primary-text);
  @media (min-width: 768px) {
    margin-bottom: 8px;

    font-size: 24px;
    line-height: 1.17;
  }
}

.modal_author {
  margin-bottom: 18px;

  font-style: italic;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.17;

  letter-spacing: -0.04em;

  color: #b4afaf;
  @media (min-width: 768px) {
    margin-bottom: 20px;

    font-size: 14px;
    line-height: 1.29;
  }
}
.modal_discription {
  margin-bottom: 14px;

  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.29;

  letter-spacing: -0.04em;
  color: var(--primary-text);
  @media (min-width: 768px) {
    margin-bottom: 20px;
    margin-right: 4px;
  }
}
.modal_shops {
  // height: 36px;
  display: flex;
  margin-bottom: 40px;
}
.modal_item:last-child {
  margin-right: 0px;
}
.modal_item {
  height: 36px;
  margin-right: 20px;
  padding: auto;
  position: relative;
}

.modal_bottom_container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
}

.modal_button {
  padding: 14px 23px;
  border: 2px solid #4f2ee8;
  border-radius: 40px;

  font-weight: 400;
  font-size: 14px;
  line-height: 18px;

  letter-spacing: -0.01em;
  text-transform: uppercase;

  color: var(--primary-text);

  animation: text-focus-in 1s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;

  @media (min-width: 768px) {
    font-weight: 700;
    font-size: 18px;
    line-height: 1.33;

    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
  }

  @include animated(background-color, color);

  &:hover {
    background-color: var(--link-focus);
    color: var(--white-color-not-chenge);
  }
}
.modal_button:disabled {
  color: grey;
  cursor: default;
  &:hover {
    background-color: inherit;
  }
}
.modal_info_text {
  margin-top: 8px;
  margin-left: auto;
  margin-right: auto;
  font-weight: 400;
  font-size: 10px;
  line-height: 1.2;
  width: 242px;

  text-align: center;
  letter-spacing: -0.04em;
  color: var(--border-form);
  opacity: 0.5;

  @media (min-width: 768px) {
    margin-left: auto;
    margin-right: auto;
    width: 324px;
    font-size: 12px;
    line-height: 1.17;
  }
}
.modal_info_text.is-hidden {
  opacity: 0;
  margin-top: 0px;
  position: absolute;
}
.modal_info_text.js-hidden {
  display: none;
}
.modal_info_text_registr {
  margin-top: 8px;
  margin-left: auto;
  margin-right: auto;
  font-weight: 400;
  font-size: 10px;
  line-height: 1.2;
  width: 242px;

  text-align: center;
  letter-spacing: -0.04em;
  color: var(--border-form);

  @media (min-width: 768px) {
    margin-left: auto;
    margin-right: auto;
    width: 324px;
    font-size: 12px;
    line-height: 1.17;
  }
}
.modal_info_text_registr.js-hidden {
  display: none;
}

@keyframes flip-in-hor-bottom {
  0% {
    -webkit-transform: rotateX(80deg);
    transform: rotateX(80deg);
    opacity: 0;
  }
  100% {
    -webkit-transform: rotateX(0);
    transform: rotateX(0);
    opacity: 1;
  }
}

@keyframes text-focus-in {
  0% {
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1;
  }
}
